@import 'styles/elements/color';

.root {
  background-color: $second;
  border-radius: 100px;
  box-shadow: rgba(255, 148, 148, .2) 0 -25px 18px -14px inset, rgba(255, 148, 148, .15) 0 1px 2px, rgba(255, 148, 148, .15) 0 2px 4px, rgba(255, 148, 148, .15) 0 4px 8px, rgba(255, 148, 148, .15) 0 8px 16px, rgba(255, 148, 148, .15) 0 16px 32px;
  color: $main-font;
  cursor: pointer;
  display: inline-block;
  padding: 7px 20px;
  text-align: center;
  text-decoration: none;
  transition: all 250ms;
  border: 0;
  font-size: 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  &:hover {
    box-shadow: rgba(255, 148, 148, .35) 0 -25px 18px -14px inset, rgba(255, 148, 148, .25) 0 1px 2px, rgba(255, 148, 148, .25) 0 2px 4px, rgba(255, 148, 148, .25) 0 4px 8px, rgba(255, 148, 148, .25) 0 8px 16px, rgba(255, 148, 148, .25) 0 16px 32px;
    transform: scale(1.05) rotate(-1deg);
  }
}